@import './reset.css';
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:wght@400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&display=swap');

:root {
    /* Colors */
    --blackpure: #000;
    --black: #171718;
    --purple: #1a0b2e;
    --dark-purple: #11071f;
    --black-border: #26292d;
    --white: #fff;
    --blue: #5c62ec;

    /* Nav */
    --nav-bg: var(--purple);
    --nav-border: var(--black-border);
    --nav-text: var(--white);
    --mobile-menu: var(--white);

    /* Header */
    --header-bg: var(--dark-purple);
    --header-text: var(--white);

    /* Footer */
    --footer-bg: var(--purple);
    --footer-text: var(--white);

    /* Light mode */
    --page-bg: var(--white);
    --text-color: var(--black);
    --accent: var(--blue);
    --title-1: var(--accent);

    --project-card-bg: var(--white);
    --project-card-text: var(--black);
    --box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);
}

.dark {
    /* Dark mode */
    --page-bg: var(--dark-purple);
    --text-color: var(--white);

    --title-1: var(--white);

    --project-card-bg: var(--black);
    --project-card-text: var(--white);
    --box-shadow: 0px 5px 35px var(--blue);
}

html,
body {
    min-height: 100vh;

    font-family: 'DM Sans', sans-serif;
    letter-spacing: -0.5px;

    /* Light mode */
    background-color: var(--page-bg);
    color: var(--text-color);
}

html {
    font-size: calc(100vw / 1920 * 14);
}

@media (max-width: 1024px) {
    html {
        font-size: calc(100vw / 768 * 14);
    }
}
@media (max-width: 767px) {
    html {
        font-size: calc(100vw / 500 * 14);
    }
}
@media (max-width: 500px) {
    html {
        font-size: calc(100vw / 375 * 14);
    }
}
#root {
    min-height: 100vh;
}

.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

/* General */

.container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1200px;
}

.none {
    display: none !important;
}

.section {
    padding: 70px 0;
}

.title-1 {
    margin-bottom: 60px;

    font-size: 60px;
    font-weight: 700;
    line-height: 1.3;
    color: var(--title-1);
    text-align: center;
}

.title-2 {
    margin-bottom: 20px;

    font-size: 40px;
    font-weight: 500;
    line-height: 1.3;
}

/* Btn */

.btn {
    display: inline-block;
    height: 48px;
    padding: 12px 28px;

    border-radius: 5px;
    background-color: var(--accent);
    color: var(--white);
    letter-spacing: 0.15px;
    font-size: 16px;
    font-weight: 500;

    transition: opacity 0.2s ease-in;
}

.btn:hover {
    opacity: 0.8;
}

.btn:active {
    position: relative;
    top: 1px;
}

/* Btn outline */

.btn-outline {
    display: flex;
    column-gap: 10px;
    align-items: center;

    height: 48px;
    padding: 12px 20px;

    border-radius: 5px;
    border: 1px solid #000;
    background-color: #fff;
    color: #000;

    transition: opacity 0.2s ease-in;
}

.btn-outline:hover {
    opacity: 0.8;
}

.btn-outline:active {
    position: relative;
    top: 1px;
}

/* Projects  */

.projects {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    column-gap: 30px;
    row-gap: 30px;
}

/* Project details */

.project-details {
    margin: 0 auto;
    max-width: 865px;

    display: flex;
    flex-direction: column;
    align-items: center;

    text-align: center;
}

.project-details__cover {
    max-width: 100%;
    margin-bottom: 40px;

    box-shadow: var(--box-shadow);
    border-radius: 10px;
}

.project-details__desc {
    margin-bottom: 30px;

    font-weight: 700;
    font-size: 24px;
    line-height: 1.3;
}

/* Content list */

.skills-list {
    font-family: 'Amatic SC';
    font-size: 5rem;
    margin: 0 5rem;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 40px;
    text-align: left;
}

.content-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 40px;
    text-align: center;
}
.content-list p {
    font-size: 2.4rem;
    letter-spacing: 0.044rem;
}

.content-list a {
    color: var(--accent);
}

.content-list__item {
    font-size: 2.4rem;
    line-height: 1.5;
}

.content-list__item p + p {
    margin-top: 0.5em;
}

@media (max-width: 620px) {
    /* Header  */

    .header {
        min-height: unset;
    }

    /*.header__title {
        font-size: 30px;
    }

    .header__title strong {
        font-size: 40px;
    }*/

    /* Nav */

    .nav-row {
        justify-content: space-between;
    }

    .dark-mode-btn {
        order: 0;
    }

    /* General */

    .section {
        padding: 40px 0;
    }

    .title-1 {
        margin-bottom: 30px;
        font-size: 40px;
    }

    .title-2 {
        margin-bottom: 10px;
        font-size: 5rem;
    }

    /* Projects */

    .project__title {
        font-size: 22px;
    }

    /* Project page */

    .project-details__desc {
        margin-bottom: 20px;
        font-size: 22px;
    }

    /* Content List */

    .content-list {
        row-gap: 20px;
    }

    .content-list__item {
        font-size: 16px;
    }

    /* Footer */

    .footer {
        padding: 40px 0 30px;
    }

    .footer__wrapper {
        row-gap: 20px;
    }

    /* Social */

    .social {
        column-gap: 20px;
    }

    .social__item {
        width: 28px;
    }
}
@media (max-width: 767px) {
    .skills-list {
        margin: 0 0.5rem;
    }
    .section {
        padding: 40px 0;
    }

    .content-list__item {
        font-size: 1.5rem;
    }
}

@media (max-width: 500px) {
    .section {
        padding: 7.5rem 0 20px;
    }

    .content-list__item {
        font-size: 2.4rem;
    }
}
