/* Header */

.header {
    font-family: 'Amatic SC';
    display: inline-flex;
    justify-content: flex-start;
    background-position: center center;
    user-select: none;
}

.header__wrapper {
    padding: 0 5rem;
}

.header__container-info {
    display: flex;
}

/* header__info  */

.header__info {
    display: inline-flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    width: 100%;
}

.header__photo-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    width: 25.8rem;
    height: 25.9rem;
    flex-shrink: 0;
    border-radius: 25.9rem;
    background: radial-gradient(50% 50% at 50% 50%, #763cac 0%, rgba(50, 15, 133, 0) 100%);
}
.header__gradient {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 38.5rem;
    height: 43.1rem;
    flex-shrink: 0;
    border-radius: 43.1rem;
    background: radial-gradient(50% 50% at 50% 50%, #763cac 0%, rgba(50, 15, 133, 0) 100%);
}

.header__description-photo {
    position: absolute;

    font-size: 1.9rem;
    font-style: normal;
    font-weight: 400;

    letter-spacing: -0.05rem;
    z-index: 100;
    top: -1rem;
    left: 24rem;
    max-width: 100%;
    min-width: 19rem;
}
.header__title_decoration-blue {
    color: #7127ba;
}

.header__title > strong > h2 > span > em {
    color: var(--text-color);
}

.header__title_vector {
    position: absolute;
    width: 23.8584rem;
    height: 5.7899rem;
    transform: rotate(-4deg);
    left: -2rem;
    top: 0.2rem;
}

.header__arrow-photo {
    position: absolute;
    width: 9.1782rem;
    left: -8rem;
    top: -1rem;
}
.header__developer-photo {
    position: relative;
    width: 16.5rem;
    height: 22.3rem;
    flex-shrink: 0;
}
.header__title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.header__title > * {
    font-weight: 400;
    line-height: normal;
    white-space: pre-line;
}

.header__title h4 {
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-size: 2.7rem;
    letter-spacing: 0.034rem;
}

.header__title h2 {
    font-size: 5rem;
    line-height: 126.664%;
    letter-spacing: 0.1rem;
}

.header__title h6 {
    font-size: 1.9rem;
    letter-spacing: 0.022rem;
}

/* header__about-me  */

.header__about-me {
    margin: 4rem 0 4rem;
}

.header__about-me > * {
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    white-space: pre-line;
}
.header__about-me > div {
    display: flex;
    flex-direction: column;
    gap: 2.4rem;
    margin-bottom: 4rem;
}

.header__about-me > div > h2 {
    font-size: 5rem;
    letter-spacing: 0.1rem;
}

.header__about-me > div > h4 {
    font-size: 2.1rem;
    letter-spacing: 0.042rem;
}

.header__about-me > h3 {
    font-size: 2.4rem;
    letter-spacing: 0.044rem;
    max-width: 90.2rem;
}

@media (max-width: 767px) {
    .header__description-photo {
        top: -4rem;
        left: 4rem;
    }
    .header__gradient {
        width: 100%;
    }
    .header__arrow-photo {
        left: -4rem;
        top: 1rem;
        transform: rotate(-80deg);
    }
    .header__wrapper {
        padding: 0 1.5rem;
    }

    .header__title h4 {
        font-size: 1.7rem;
    }

    .header__title h2 {
        font-size: 3rem;
    }

    .header__title h6 {
        font-size: 1.1rem;
    }
    .header__about-me > div {
        gap: 0.5rem;
        margin-bottom: 1rem;
    }

    .header__about-me > div > h2 {
        font-size: 3rem;
    }
    .header__about-me > div > h4 {
        font-size: 1.5rem;
    }
    .header__about-me > h3 {
        font-size: 1.5rem;
    }

    .header__about-me {
        margin: 1.5rem 0 0.5rem;
    }
    .header__wrapper {
        padding: 0 0.5rem;
    }
}
@media (max-width: 500px) {
    .header__info {
        margin-top: 5.5rem;
    }
    .header__title h6 {
        font-size: 0.8rem;
    }
}
