/* Nav */

.nav {
    padding: 20px 0;
    background-color: var(--nav-bg);
    border-bottom: 1px solid var(--nav-border);
    color: var(--nav-text);
    letter-spacing: normal;
}

.nav-row {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    column-gap: 30px;
    row-gap: 20px;
    flex-wrap: wrap;
}

.logo {
    margin-right: auto;
    color: var(--nav-text);
    font-size: 24px;
    font-family: 'Poppins', sans-serif;
}

.logo strong {
    font-weight: 700;
}

.nav-list {
    display: flex;
    flex-wrap: wrap;
    row-gap: 10px;
    align-items: center;
    column-gap: 40px;
    font-size: 16px;
    font-weight: 500;
    font-family: 'Poppins', sans-serif;
}

.nav-list__link {
    color: var(--nav-text);
    transition: opacity 0.2s ease-in;
}

.nav-list__link:hover {
    opacity: 0.8;
}

.nav-list__link--active {
    position: relative;
}

.nav-list__link--active::before {
    content: '';

    position: absolute;
    left: 0;
    top: 100%;

    display: block;
    height: 2px;
    width: 100%;
    background-color: var(--accent);
}

/*	mobile-menu  */

.mobile-menu__checkbox {
    display: none;
}
.menu-bottom {
    cursor: pointer;
    float: right;
    padding: 10px;
}
.menu-bottom__line {
    background: var(--mobile-menu);
    display: block;
    height: 2px;
    position: relative;
    width: 30px;
    height: 3.5px;
}

.menu-bottom__line::before,
.menu-bottom__line::after {
    background: var(--mobile-menu);
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    transition: all 0.2s ease-out;
    width: 100%;
}

.menu-bottom__line::before {
    top: 5px;
}

.menu-bottom__line::after {
    top: -5px;
}

/* Hide checkbox */

.mobile-menu__checkbox:checked ~ .nav-list {
    position: absolute;
    display: flex;
    justify-content: center;
    height: 100%;
    top: 66px;

    width: 100%;
    left: 0;
    background-color: rgb(10%, 4%, 18%, 0.8);
}

.mobile-menu__checkbox:checked ~ .menu-bottom .menu-bottom__line {
    background: transparent;
}

.mobile-menu__checkbox:checked ~ .menu-bottom .menu-bottom__line::before {
    transform: rotate(-45deg);
    top: 0;
}

.mobile-menu__checkbox:checked ~ .menu-bottom .menu-bottom__line::after {
    transform: rotate(45deg);
    top: 0;
}

@media (max-width: 500px) {
    .nav {
        position: fixed;
        width: 100%;
        z-index: 10000;
    }
    .nav-list {
        display: none;
    }
    .nav-row {
        justify-content: space-between;
        gap: 10px;
    }
    .logo {
        margin: 0 auto;
    }
}
